@import "../../global.scss";

.intro {
    background-color: white;
    display: flex;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;

        .imgContainer {
            width: 700px;
            height: 700px;
            overflow: hidden;
            background-color: #DAADA6; 
            border-radius: 50%;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            float: left;


            img {
                height: 100%;

                @include mobile {
                    height: 60%;
                    width: auto;
                }
            }
        }

    }
    .right {
        flex: 0.5;
        position: relative;
        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1 {
                font-size: 60px;
                margin: 10px 0;

                @include mobile {
                    font-size: 40px;
                }
            }
            h2 {
                font-size: 30px;
                @include mobile {
                    font-size: 30px;
                }
            }
            h3 {
                font-size: 30px;

                span {
                    font-size: inherit;
                    color: #DAADA6;
                }

                .ityped-cursor {
                    animation:  blink 1s infinite;
                }
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }

                @include mobile {
                    font-size: 20px;
                }
            }
        }
        a {
            position: absolute;
            bottom: 10px;
            left: 35%;

            .icon {
                font-size: 50px;
                color: black;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                to{
                    opacity: 0;
                }
            } 
                
          
        }
    }

}